import { createStore } from 'vuex'
import router          from "../router"
import fb              from '../firebase/index' 

export default createStore({
  state: {

  	tareas:[],
  	tarea:{ 
  			id:'',
  			nombre:'',
  			categorias:[],
  			estado:'',
  			numero:0
  		}
  },
  mutations: {

    cargar(state,payload){
      state.tareas =  payload;  
    },
  	set(state,payload){ 
  		state.tareas.push(payload)  
  	},

  	//Filter crea un nuevo array unicamente con registros que coincide el filtro
  	//Sobreescribimos el ereglo
  	eliminar(state, payload){ 
     state.tareas = state.tareas.filter(item => item.id !==payload)   
  	},

    tarea(state,payload){ 
      if (!state.tareas.find(item => item.id ===payload)) 
      { 
        router.push('/')
        return
      } 
      state.tarea = state.tareas.find(item => item.id ===payload)
    },

    update(state,payload){ 
      state.tareas = state.tareas.map(item => item.id ===payload.id?payload:item)
      router.push('/')       
    }


  },





  actions: {
    async cargarLocalStorage({commit})
      {
        try
        {  
          const ciudades = await fb.collection('ciudad').get();     
          const ar_tareas = []; 

          for (let doc of ciudades.docs)
          {  
           ar_tareas.push(doc.data());  
          }   
          commit('cargar',ar_tareas);    
        }
        catch(error) { console.log("error funcion - cargarLocalStorage: ", error); }         
      },
  
 
    async setTareas({commit},tarea)
      { 
        try
        {   
  
          await  fb.collection("ciudad").doc(tarea.id).set({
          id : tarea.id , 
          nombre: tarea.nombre,
          categorias: tarea.categorias,
          estado: tarea.estado,
          numero: tarea.numero
          });  // id propio 
          //await  fb.collection("ciudad").add({ tarea });  // id generado 
        }
        catch(error) { console.log("error funcion - setTarea: ", error); }
      },


 

    async deleteTareas({commit},id){
      try
        {
          await fb.collection('ciudad').doc(id).delete();     
          commit('eliminar',id)      
        }
        catch(error) { console.log("error funcion - setTarea: ", error); }
    },


    setTarea({commit},id){
      commit('tarea',id)
    }, 
 
    async updateTarea({commit},tarea){ 

        try
        {     
          await fb.collection('ciudad').doc(tarea.id).update(tarea);
          commit('update',tarea);   
          
        }
        catch(error) { console.log("error funcion - updateTarea: ", error); } 
    } 

  },
  modules: {
  }
})
